@import 'settings/Global.scss';


#objects_main {
    position: absolute;
    top: calc(#{$sidebarright_element_height} * 2);
    //background-color: yellow;
    width: $sidebarright_element_innerwidth;
    margin: $box_margin;
    border: solid $darkgrey $box_border;
    height: $sidebarright_element_innerheight;
}

#objects_header {
    width: $sidebarright_element_innerwidth;
    height: 25px;
    background-color: $darkgrey;
    color: $lightgrey
}

#objects_header_logo {
    position: absolute;
    height: 25px;
    width: 25px;
}

#objects_header_name {
    position: absolute;
    left: 30px;
    top: 4px;
    font-family: "Arial", Helvetica, sans-serif;
    font-size: 14px;
}

#objects_body {
    width: $sidebarright_element_innerwidth;
    height: calc(#{$sidebarright_element_innerheight} - 25px);
    background-color: $grey;
    color: $lightgrey;
    overflow-y: scroll;
}

.objects_elem {
    position: relative;
    $padding: 5px;
    width: calc(#{$sidebarright_element_innerwidth} - 2 * #{$padding});
    height: 40px;
}

.objects_elem:hover {
    background-color: $blue;
}

.objects_elem_icon {
    height: 40px;
}

.objects_elem_text {
    position: absolute;
    left: 45px;
    top: 12px;
}