@import 'settings/Global.scss';


#server_main {
    position: absolute;
    top: calc(#{$window_height} - #{$footer_height} - #{$header_height} - #{$sidebarleft_server_height} + 50px);
    width: $sidebarright_element_innerwidth;
    margin: $box_margin;
    border: solid $darkgrey $box_border;
    height: $sidebarleft_server_innerheight;
}

#server_header {
    width: $sidebarright_element_innerwidth;
    height: 25px;
    background-color: $darkgrey;
    color: $lightgrey
}

#server_header_logo {
    position: absolute;
    height: 25px;
    width: 25px;
}

#server_header_name {
    position: absolute;
    left: 30px;
    top: 4px;
    font-family: "Arial", Helvetica, sans-serif;
    font-size: 14px;
}

#server_body {
    width: $sidebarright_element_innerwidth;
    height: calc(#{$sidebarright_settings_innerheight} - 25px);
    font-family: "Arial", Helvetica, sans-serif;
    background-color: $grey;
    color: $lightgrey;
    overflow-y: scroll;
}

.server_item {
    position: relative;
    background-color: $grey;
    margin: 10px;
    padding: 5px;
    border: solid $darkgrey 1px;
    border-radius: 3px;
    width: 150px;
}

.server_item:hover {
    box-shadow:0px 0px 5px 0px cyan;
}

.server_item_icon {
    height: 15px;
}

.server_item_text {
    position: absolute;
    left: 30px;
    top: 6px;
}